console.log(process.env.API_URL);

const MEMBERS_API_URL = `${process.env.API_URL}/members`;
const BOOKS_API_URL = `${process.env.API_URL}/books`;
const ISSUES_API_URL = `${process.env.API_URL}/issues`;
const RETURNS_API_URL = `${process.env.API_URL}/returns`;

sendHttpRequest(MEMBERS_API_URL, (response)=>{console.log(response)});
sendHttpRequest(BOOKS_API_URL, (response)=>{console.log(response)});

function sendHttpRequest(url: string,
                         successFn: (response: string)=> void,
                         errorFn?: (status: number)=> void):void{

    const http = new XMLHttpRequest();

    http.onreadystatechange = ()=>{
        if (http.readyState === http.DONE) {
            if (http.status===200) {
                successFn(http.responseText);
            } else {
                if (errorFn) errorFn(http.status)
            }
        }
    }

}